body {
    background-color: #f1f5f9;
    color: #343a40;
    height: 100%; }
.avatar-style {
    display: block;
    float: right;
    max-height: 500px;
    @media(max-width: 980px) {
        max-width: 300px; } }
.subtitle {
    color: #929ca6; }
.nav-leftbar {
    margin: auto 10px; }
.menu {
    li {
        list-style: none;
        color: #929ca6;
        &:hover {
            color: #2b3035; }
        a {
            margin: 5px 0px;
            color: #929ca6;
            &:hover {
                text-decoration: none;
                color: #2b3035;
                cursor: pointer;
                font-weight: 400; } } } }
.btn-link {
    text-decoration: none;
    background-color: #757d85;
    color: #ffffff;
    padding: 14px 25px;
    text-align: center;
    display: inline-block;
    border-radius: 5px;
    &:hover, &:active {
        background-color: #192734;
        text-decoration: none;
        color: #ffffff; } }
.main-section {
    height: calc( 100vh - 60px ); }
.dropdown-content {
    display: none;
    a {
        float: none;
        display: block; } }
.dropdown {
    cursor: pointer;
    margin: 5px 0px;
    padding: 0.5rem 1rem;
    text-decoration: none;
    outline: none;
    border: none;
    &:hover {
        display: block;
        .dropdown-content {
            display: block !important; } }
    .dropdown-content {
        position: absolute;
        min-width: 160px;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 200;
        border-radius: 9px;
        background-color: #f1f5f9;
        a {
            text-decoration: none;
            padding: 12px 16px; } } }
.photo-col {
    display: flex;
    .photo-img {
        margin-left: auto;
        margin-right: auto;
        width: 300px;
        height: 300px;
        float: right;
        @media(max-width: 980px) {
            width: 200px;
            height: 200px; } } }
.illustration-card {
    width: 250px;
    height: 250px;
    margin: auto;
    box-sizing: content-box; }
.illustration-title {
    margin-top: -20px;
    text-align: center;
    text-decoration: none;
    opacity: 0;
    color: black;
    background: linear-gradient(to top, rgba(255,255,255, 1), rgba(255,255,255,0.1)); }
.illustration-link {
    text-decoration: none;
    color: black;
    &:hover {
        .illustration-title {
            opacity: 1; } } }
.illustration-gallery {
    width: 250px;
    height: 250px; }
.comic-principal {
    max-width: 500px;
    @media(max-width: 980px) {
        max-width: 400px; } }
.btn-ant {
    width: 20px;
    height: 20px;
    cursor: pointer; }

.btn-sig {
    width: 20px;
    height: 20px;
    cursor: pointer; }

.gallery-container {
    max-width: fit-content; }

.g-element {
    display: inline-flex; }

.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px; }

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg); }

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg); }

.title-gallery {
    text-align: center;
    color: #6e7b87;
    font-weight: 300; }

.social-box {
    text-align: right; }

.icon-sn {
    width: 20px;
    height: 20px;
    margin: 5px 7px; }
